
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Confirm from 'vux/src/components/confirm/index.vue'

export default {
name:'doctorNews',
components:{Swipeout, SwipeoutItem, SwipeoutButton,XDialog,Confirm},
directives: {TransferDom},
data(){
return {
tabItem:true,
userId:'',
errorImg:'this.src="' + require('../../assets/header333x.png') + '"' ,
news:[],
systemNews:[],
doctorNews:[],
showContent:false,
showDelConfirm:false
}
},
mounted(){
this.userId = (localStorage.getItem('md') || tools.getRequestParam('managerId') || tools.getCookie('user_id'));
if(!this.userId || this.userId=='undefined'){
sessionStorage.setItem('tempURL',window.location.href);
tools.next('/m/login.html?tempURL='+window.location.href);
return false;
}// 此时可以跳转到登陆，等登陆后在跳转回来
this.getNews(3); 
},
methods:{
getNews(type){
let vm = this;
this.$http.axios({method:'POST',url:'/message/getPageMessages',data:{messageTo:this.userId,type:type,pageSize:1,pageNumber:1000}}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200)){
vm.news = data.items;
}else{
console.log(data.resultCode);
vm.showToast = true;
vm.toastMsg = data.resultCode.message;
}
},(error) => {
console.log(error);
vm.showToast = true;
vm.toastMsg = '操作失败';
})
},
toggleTab(type){
this.tabItem = type;
if(type){
this.getNews(3);
}else{
this.getNews(2);
}
},
deleteFn(id){
this.showDelConfirm = true;
this.delId = id;
},
goToDetail(content){
document.getElementById('contentText').innerHTML = content;
const imgs = document.querySelectorAll('#contentText img')
for(var i=0;i<imgs.length;i++){
imgs[i].className = 'img-responsive';
imgs[i].style.display = 'block';
imgs[i].style.maxWidth = '100%';
imgs[i].style.height = 'auto';
}
this.showContent = true;
},
onJGConfirm(){
let vm = this;
this.$http.axios({method:'get',url:'/message/delete?id='+this.delId}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200)){
vm.showToast = true;
vm.toastMsg = '删除成功';
vm.news = vm.news.filter(item => item.id!=vm.delId);
}else{
console.log(data.resultCode);
vm.showToast = true;
vm.toastMsg = data.resultCode.message;
}
},(error) => {
console.log(error);
vm.showToast = true;
vm.toastMsg = '操作失败';
})
}
}
}



